
  <div>
    <div
      v-if="showLauncher"
      class="sc-launcher"
      :class="{ opened: isOpen }"
      :style="{ backgroundColor: colors.launcher.bg }"
      @click.prevent="isOpen ? close() : openAndFocus()"
    >
      <div
        v-if="newMessagesCount > 0 && !isOpen"
        class="sc-new-messsages-count"
      >
        {{ newMessagesCount }}
      </div>
      <img
        v-if="isOpen"
        class="sc-closed-icon"
        :src="icons.close.img"
        :alt="icons.close.name"
      />
      <img
        v-else
        class="sc-open-icon"
        :src="icons.open.img"
        :alt="icons.open.name"
      />
    </div>
    <ChatWindow
      :message-list="messageList"
      :on-user-input-submit="onMessageWasSent"
      :participants="participants"
      :title="chatWindowTitle"
      :content-editable="contentEditable"
      :is-open="isOpen"
      :show-emoji="showEmoji"
      :show-launcher="showLauncher"
      :show-file="showFile"
      :show-header="showHeader"
      :placeholder="placeholder"
      :show-typing-indicator="showTypingIndicator"
      :colors="colors"
      :always-scroll-to-bottom="alwaysScrollToBottom"
      :message-styling="messageStyling"
      @close="close"
      @scrollToTop="$emit('scrollToTop')"
      @onType="$emit('onType')"
      @edit="$emit('edit', $event)"
      @remove="$emit('remove', $event)"
      @option="$emit('option', $event)"
    >
      <template #header>
        <slot name="header"> </slot>
      </template>
      <template #user-avatar="scopedProps">
        <slot
          name="user-avatar"
          :user="scopedProps.user"
          :message="scopedProps.message"
        >
        </slot>
      </template>
      <template #text-message-body="scopedProps">
        <slot
          name="text-message-body"
          :message="scopedProps.message"
          :message-text="scopedProps.messageText"
          :message-colors="scopedProps.messageColors"
          :me="scopedProps.me"
        >
        </slot>
      </template>
      <template #system-message-body="scopedProps">
        <slot name="system-message-body" :message="scopedProps.message"> </slot>
      </template>
      <template #text-message-toolbox="scopedProps">
        <slot
          name="text-message-toolbox"
          :message="scopedProps.message"
          :me="scopedProps.me"
        >
        </slot>
      </template>
    </ChatWindow>
  </div>
